.App {
    text-align: center;
    background: linear-gradient(132deg, #2800ff, #ff0000, #f9ff00);
    background-size: 600% 600%;
    -webkit-animation: GradientAnimation 20s ease infinite;
    -moz-animation: GradientAnimation 20s ease infinite;
    -o-animation: GradientAnimation 20s ease infinite;
    animation: GradientAnimation 20s ease infinite;
}


@-webkit-keyframes GradientAnimation {
    0% {
        background-position: 32% 0%
    }
    50% {
        background-position: 69% 100%
    }
    100% {
        background-position: 32% 0%
    }
}

@-moz-keyframes GradientAnimation {
    0% {
        background-position: 32% 0%
    }
    50% {
        background-position: 69% 100%
    }
    100% {
        background-position: 32% 0%
    }
}

@-o-keyframes GradientAnimation {
    0% {
        background-position: 32% 0%
    }
    50% {
        background-position: 69% 100%
    }
    100% {
        background-position: 32% 0%
    }
}

@keyframes GradientAnimation {
    0% {
        background-position: 32% 0%
    }
    50% {
        background-position: 69% 100%
    }
    100% {
        background-position: 32% 0%
    }
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.animationHeightChangerSmall {
    animation: heightChangerSmall 0.3s forwards;
}

.animationHeightChangerBig {
    animation: heightChangerBig 0.3s forwards;
}

@keyframes heightChangerSmall {
    from {
        height: 100px;
    }
    to {
        height: 50px;
    }
}

@keyframes heightChangerBig {
    from {
        height: 50px;
    }
    to {
        height: 100px;
    }
}


@-webkit-keyframes Rotate {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}

@-moz-keyframes Rotate {
    from {
        -moz-transform: rotate(0deg);
    }
    to {
        -moz-transform: rotate(360deg);
    }
}

@-ms-keyframes Rotate {
    from {
        -ms-transform: rotate(0deg);
    }
    to {
        -ms-transform: rotate(360deg);
    }
}

